.gameInterface {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    height: 100%;
    width: 25%;
}

.gamePieces {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;

    height: 100%;
    width: 100%;
}

.gameInterface h2 {
    font-size: 3rem;
}

@media only screen and (max-width: 985px) {
    .Computer {
        flex-direction: column-reverse;
    }

    .gameInterface {
        height: 100%;
        width: 100%;
    }

    .gameInterface h2 {
        font-size: 2.5rem;
    }

    .gamePieces {
        flex-direction: row;
    }
}